* {
  box-sizing: border-box;
}
.contact-wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.contact {
  font-size: 17px;
  background-color: rgb(141, 223, 243);
  border: dashed rgb(169, 218, 247);
  display: grid;
  justify-content: center;
  align-items: center;
  width: 400px;
  height: 90px;
  margin-top: 10px;
  margin-bottom: 10px;
}
.contact-name {
  color: rgb(30, 3, 92);
  opacity: 0.9;
  font-size: 25px;
  font-weight: 600;
}
.contact-phoneNumber {
  color: rgb(2, 35, 78);
  position: relative;
  z-index: 1;
  margin-top: auto;
}
.search-form {
  border: solid rgb(2, 17, 102);
  font-size: 15px;
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 400px;
  height: 70px;
  background-color: rgb(40, 82, 221);
  cursor:pointer;
}
